								height="100%"
								ViewWidth="31"
								ViewHeight="31"
								class="services--right"
				><icon-arrow></icon-arrow>
				</icon-base>
			</div>
			<div v-if="selected==5" class="services--strelka services--novisi services--right text-type-big text-align-center text-size-xxl">
				&#215;
			</div>
		</div>

		<div class="d-flex d-flex-j-c" v-bind:class="{ 'main--blur': services}">
			<div class="main--box">

				<div class="box ">
					<div class="box--content">
						<div class="office">

							<div class="office--top office-color-bright">

								<div class="office--element office-pos-abs office-pos-abs-center office-main-5 office--money ">
									<div class="money--black">

