														ViewHeight="357"
										><icon-board /></icon-base>

									</div>

								</div>

								<div @click="crashLamp" @mouseover="visibleLamp" @mouseleave="unvisible" class="office--nasadkalamp office-pos-abs office-pos-abs-center"></div>

								<div class="office--element office-pos-abs office-pos-abs-center office-main-4 office--lamp">

