										<icon-base
														width="100%"
														height="100%"
														ViewWidth="1920"
														ViewHeight="2700"
										><icon-lamp
														ref="lamp"
														:select="boolLamp"
										/></icon-base>

									</div>

								</div>

								<div class="office--element office-pos-abs office-main-2 office-pos-abs-center office--monitor">

									<div class="office--monitor-child monitor">

										<icon-base
														width="100%"
														height="100%"
														ViewWidth="61"
														ViewHeight="61"
										><icon-imac /></icon-base>

									</div>

								</div>

								<div class="office--element office-pos-abs office--radio">
									<radio-anim>
									</radio-anim>
								</div>

								<div class="office--element office-pos-abs office-main-2 office--tea">

									<div @click="crashTea"  class="office--tea-child tea">
										<icon-base
														width="100%"
														height="100%"
														ViewWidth="306"
														ViewHeight="1230"
										><icon-tea
														ref="tea"
														:select="boolLamp"
										/></icon-base>
									</div>

								</div>

								<div class="office--element office-pos-abs office--smoke">

									<div class="office--smoke-child smoke">

										<template name="fade">
											<icon-base
															v-if="isHidden"
															width="100%"
															height="100%"
															ViewWidth="600"
															ViewHeight="876"
											><icon-smoke /></icon-base>
										</template>

									</div>

								</div>

								<div class="office--element office-pos-abs office-main-3 office-pos-abs-center office--table">

									<div class="office--table-child table">

										<icon-base
														width="100%"
														height="100%"
														ViewWidth="664"
														ViewHeight="611"
										><icon-table /></icon-base>

									</div>

								</div>

								<div class="office--element office-pos-abs office-main-2 office--avatar">

									<div class="office--avatar-child avatar">

										<icon-base
														width="100%"
														height="100%"
														ViewWidth="2702"
														ViewHeight="1882"
										><icon-avatar /></icon-base>

									</div>

								</div>

								<div class="office--element office-pos-abs office-main-1 office--cactus">

									<div class="office--cactus-child cactus">

										<icon-base
														width="100%"
														height="100%"
														ViewWidth="567"
														ViewHeight="293"
										><icon-cactus /></icon-base>

									</div>

								</div>

							</div>
							<div class="office--bottom office-color-white">
							</div>
						</div>
					</div>
				</div>

			</div>
		</div>
		<div class="contact" v-bind:class="{ 'main--blur': services}">

			<div class="contact--group">
				<div class="contact--man">
					<div class="contact--circle">
						<div class="contact--man-child man">
							<icon-base
											width="100%"
											height="100%"
											ViewWidth="30"
											ViewHeight="40"
							><icon-head-man
								ref="head"
							></icon-head-man></icon-base>
						</div>
					</div>
				</div>

				<div class="contact--text">

					<div class="contact--null">

					</div>

					<div class="text text-color-white text-size-m contact--text-top">
						Привет, наша компания занимается разработкой систем управления предприятием,
						разработкой облачных сервисов,
						разработкой мобильных приложений,
						разработкой систем учета,
						разработкой систем документооборота.
					</div>
					<div class="contact--question contact--text-bot">

						<div @click="visibleServices" class="text text-color-white text-align-center text-size-m contact--button button">
							Какие задачи вы решаете ?
						</div>

						<div @mouseover="visibleMoney" @mouseleave="unvisibleMoney" class="text text-color-white text-align-center text-size-m contact--button button">
							Какова стоимость ваших услуг ?
						</div>

					</div>

				</div>

			</div>

		</div>


	</div>
</template>

<script>
	import { TweenMax } from 'gsap'

	import IconBase from './icon/IconBase.vue'
	import IconImac from './icon/IconImac.vue'
	import IconTable from './icon/IconTable.vue'
	import IconBoard from './icon/IconBoard.vue'
	import IconLamp from './icon/IconLamp.vue'
	import IconAvatar from './icon/IconAvatar.vue'
	import IconCactus from './icon/IconCactus.vue'
	import IconHeadMan from './icon/IconHeadMan.vue'
	import IconSmoke from './icon/IconSmoke.vue'
	import IconMoney from './icon/IconMoney.vue'
	import iconTea from './icon/iconTea.vue'
	import IconServices from './icon/IconServices.vue'
	import IconFactory from './icon/IconFactory.vue'
	import IconCloud from './icon/IconCloud.vue'
	import IconPhone from './icon/IconPhone.vue'
	import IconAccounting from './icon/IconAccounting'
	import IconFile from './icon/IconFile.vue'
	import IconArrow from './icon/IconArrow.vue'
	import IconClose from './icon/IconClose.vue'

	import CrashTea from './iconAmination/CrashTea.vue'
	import RadioAnim from './iconAmination/RadioAnim.vue'

	export default {

		data: function() {
			return {
				isHidden: true,
				isHiddenLamp: false,
				lamp: undefined,
				money: undefined,
				tea: undefined,
				head: undefined,
				selected: 1,
				services: false,

				centerX:0,
				leftGran:0,
				rightGran:0,

				x:0,
				y:0,

			}
		},
		mounted() {
			this.lamp = this.$refs.lamp;
			this.head = this.$refs.head;
			this.money = this.$refs.money;
			this.tea = this.$refs.tea;

			this.start();
		},
		created() {
			window.addEventListener('resize', this.windowSize)
			this.windowSize();
		},
		destroyed() {
			window.removeEventListener('resize', this.windowSize)
		},
		name: 'DeveloperRoom',
		components:{
			IconBase,
			IconImac,
			IconTable,
			IconBoard,
			IconLamp,
			IconAvatar,
			IconCactus,
			IconHeadMan,
			IconSmoke,
			IconMoney,
			iconTea,
			IconServices,
			IconFactory,
			IconCloud,
			IconPhone,
			IconAccounting,
			IconFile,
			IconArrow,
			IconClose,

			CrashTea,
			RadioAnim
		},
		methods: {
			killSmoke(){
				this.isHidden = false;
			},
			visibleLamp() {
				this.isHiddenLamp = true;
			},
			unvisible() {
				this.isHiddenLamp = false;
			},
			crashLamp() {
				this.lamp.crash();
			},
			visibleMoney(){
				this.money.hello();
			},
			unvisibleMoney() {
				this.money.bay();
			},
			crashTea() {
				this.killSmoke();
				this.tea.crash();
			},
			plus() {
				if (this.selected < 5) {
					this.selected += 1;
				}
			},
			minus() {
				if (this.selected > 1) {
					this.selected -= 1;
				}
			},
			visibleServices() {
				this.services = true;
				this.stop();
			},
			UnvisibleServices() {
				this.services = false;
				this.start();
			},
			cardMove(e) {

				if (e.center.x < this.leftGran || e.center.x > this.rightGran) {
					TweenMax.to('#bounce--card', 0.25, { opacity: 0.25 });
				}
				else {
					TweenMax.to('#bounce--card', 0.25, { opacity: 1 });
				}

				var X = e.center.x - this.centerX;

				TweenMax.to('#bounce--card', 0.75, { x: X, transformOrigin:"end", rotation: X/20, y: (X/50)*(X/50)});
			},
			cardStart(e) {

				if (e.center.x > this.rightGran) {
					this.plus();
				}
				else if (e.center.x < this.leftGran) {
					this.minus();
				}
				else {
					TweenMax.to('#bounce--card', 0.75, { x: 0, y: 0, transformOrigin:"end", rotation: 0 });
					TweenMax.to('#bounce--card', 0.25, { opacity: 1 });
				}
			},
			windowSize() {
				var width = window.innerWidth;

				this.centerX = width/2;
				this.leftGran = width * 0.2;
				this.rightGran = width - this.leftGran;
			},
			stop() {
				this.head.stop();
				this.lamp.stop();
			},
			start() {
				this.lamp.start();
				this.head.start();
			}
		},
		computed: {
			boolLamp() {
				return this.isHiddenLamp;
			}
		},
	}
</script>

<style lang="scss">
	$color-back: #d4cbb9;
	$color-floor: #f2f2f2;
  $color-footer: #93c5c6;

	@function calcFluidFontSize($f-min, $f-max, $v-min, $v-max) {
		$k: ($f-max - $f-min)/($v-max - $v-min);
		$b: $f-min - $k * $v-min;

		$b: $b * 1px;

		@return calc( #{$k} * 100vw + #{$b} );
	}

	@keyframes button {
		from {
			background-color:#93c5c6;
			color: white;
		}
		to {
			background-color: white;
			color: #93a6c6;
		}
	}

	@keyframes yav {
		from {
			opacity: 0;
		}
		to {
			opacity: 1;
		}
	}

	@keyframes Noyav {
		from {
			opacity: 1;
		}
		to {
			opacity: 0;
		}
	}

	.box {
		width: 100%;
		padding-top: 37%;
		position: relative;

		&--content {
			position: absolute;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
		}

		&--one {

		}

		&--two {
			z-index: 1;
		}

		&--three {

		}

	}

	.button {
		padding: 0.5vw 1vw;
		border-radius: 10px;
		cursor: pointer;
		margin-right: 1.5vw;
		margin-left: 1.5vw;

		&:hover {
			animation: button  0.15s 1 ease-in-out;
			background-color: white;
			color: #93a6c6;
		}
	}

	.contact {
		z-index: 10;
		background: linear-gradient(90deg, #93c5c6, #93a6c6 90%);
		height: 100%;
		position: relative;

		&--man-child {
			width: 55%;
		}

		&--man {
			width: 20%;
			height: 25%;
			z-index: 1;
			float: left;
			shape-outside: circle(50%);
			margin-right: 3%;
			margin-left: 3%;
		}

		&--null {
			height: 6vw;
		}

		&-d-flex {
			display: flex;
			justify-content: space-between;
		}

		&--question {
			width: 100%;
			display: flex;
			justify-content: center;
			margin-top: 1vh;
			position: absolute;
		}

		&--circle {
			width: 16vw;
			height: 16vw;

			background-color: $color-footer;
			border-radius: 50%;
			display: flex;
			justify-content: center;
			background: linear-gradient(0deg, #93c5c6 35%, #93a6c6);

		}

		&--group {
			position: absolute;
			width: 100%;
			top: -8vw;
		}

		&--text-top {
			padding-top: 2.5vw;
			padding-bottom: 2.5vw;
			padding-right: 1.5vw;
			padding-left: 1.5vw;
			background: linear-gradient(90deg, #93c5c6, #5470a1);
		}

		&--romb {
			border-radius: 50%;
		}

	}

	.d-flex {
		display: flex;

		&-j-c {
			justify-content: center;
		}

		&-j-b {
			justify-content: space-between;
		}

		&-j-a {
			justify-content: space-around;
		}
	}

	.img--logo {
		width: 100%;
		height: 100%;
	}

	.main {
		background-color: #d4cbb9;
		display: flex;
		flex-direction: column;
		height: 100%;
		overflow: hidden;
		font-family: 'Rubik', sans-serif;

		&--box {
			width: 100%;
		}

		&--blur {
			filter: blur(5px);
		}

		&--contact {
			height: 100vh;
			width: 100vw;
		}
	}

	.office {
		height: 100%;

		&--avatar {
			top: 13%;
			right: 1%;
			width: 23%;
		}

		&--bottom {
			height: 30%;
			width: 100%;
		}

		&--board {
			top: 15%;
			left: 2%;
			width: 25%;
		}

		&--radio {
			left: 40%;
			top: 69%;
			width: 4%;
		}

		&-color-white {
			background-color: $color-floor;
		}

		&-color-bright {
			background: linear-gradient(180deg, #d4cbb9, #c2b9a9);
		}

		&--cactus {
			width: 50%;
			right: -13%;
			top: 50%;
		}

		&--money {
			width: 25%;
			pointer-events: none;
		}

		&--element-width-100 {
			width: 100%;
		}

		&--element-width-75 {
			width: 100%;
		}

		&--element-width-50 {
			width: 100%;
		}

		&--element-width-25 {
			width: 100%;
		}

		&-flex-center {
			display: flex;
			justify-content: center;
		}

		&--logo {
			width: 15%;
			height: 15%;
			top: 4%;
			left: 7%;
		}

		&--lamp {
			z-index: 1;
			transform: translateY(-1%);
			width: 50vw;
			pointer-events: none;
		}

		&--monitor {
			top: 44%;
			width: 10%;
			left: 5%;
		}

		&-main-1 {
			z-index: 1;
		}

		&-main-2 {
			z-index: 2;
		}

		&-main-3 {
			z-index: 3;
		}

		&-main-4 {
			z-index: 4;
		}

		&-main-5 {
			z-index: 5;
		}

		&-main-6 {
			z-index: 6;
		}

		&-main-7 {
			z-index: 7;
		}

		&-main-100 {
			z-index: 100;
		}

		&--nasadkalamp {
			cursor: pointer;
			width: 6%;
			height: 20%;
			z-index: 2;
		}

		&-pos-abs {
			position: absolute;
		}

		&-pos-abs-center {
			left: 50%;
			transform: translateX(-50%);
		}

		&-pos-rel {
			position: relative;
		}

		&--pause {
			width: 25px;
		}

		&--play {
			width: 25px;
		}

		&--smoke {
			left: 55.5%;
			top: 55%;
			width: 3%;
		}

		&--top {
			height: 70%;
			width: 100%;
			position: relative;
