	.services{
		z-index: 100;
		position: fixed;
		display: flex;
		width: 100%;
		height: 100%;
		align-items: center;
		justify-content: center;
		user-select: none;

		&--element {
			background-color: #F2F2F2;
			border-radius: 3%;
			width: 35vw;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
		}

		&--element-none {
			display: none;
		}

		&--iconFactory {
			margin-top: 1%;
			width: 100%;
		}

		&--iconCloud {
			margin-top: 10%;
			width: 60%;
		}

		&--strelka {
			padding: 30px;
			cursor: pointer;
			width: 2vw;
			height: 2vw;
			background-color: #F2F2F2;
			border-radius: 50%;
			box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
