			}
			margin-left: 3vw;
			margin-right: 3vw;
		}

		&--text {
			width: 70%;
			margin-top: 30%;
			margin-bottom: 10%;
		}

		&--novisi {
			opacity: 0;
			cursor: default;
		}


		&--close {
			cursor: pointer;
			margin-top: 3%;
			transform: translateX(15vw);
			width: 2vw;
			&:hover {
				color: #e97451;
			}
		}

		&--right {
			transform: rotate(180deg);
		}
	}

	.text {

		&-color-white {
			color: white;
		}

		&-size-m {
			font-size: calcFluidFontSize(10, 16, 480, 1280);
		}

		&-size-l {
			font-size: calcFluidFontSize(20, 25, 480, 1280);
		}

		&-size-xxl {
			font-size: calcFluidFontSize(35, 50, 480, 1280);
		}

		&-align-center {
			text-align: center;
		}

		&-align-start {
			text-align: start;
		}

		&-align-end {
			text-align: end;
		}

		&-type-big {
			font-family: 'Russo One', sans-serif;
		}

	}

	.bounce-enter-active {
