<script>

	import { TimelineMax, Elastic } from 'gsap'

	export default {
		data: function () {
			return {
				tl:undefined,
				tl2: undefined,
				tl3: undefined,
			}
		},
		mounted() {
			this.tl = new TimelineMax({repeat:-1});
			this.tl2 = new TimelineMax({repeat:-1});
			this.tl3 = new TimelineMax({repeat:-1});
		},
		methods: {
			start() {
				var eye = this.$refs.eye;
				var duga = this.$refs.duga
				var head = this.$refs.head

				this.tl.to(eye, 0.25, {scaleY:0, transformOrigin:"0% 50%"});
				this.tl.to(eye, 0.25, {scale:1, transformOrigin:"100% 50%"});
				this.tl.to(eye, 3.5, {scale:1, transformOrigin:"100% 50%"});

				this.tl2.set(duga,{y:0.5})
				this.tl2.to(duga,0.25,{y:-0.05})
				this.tl2.to(duga,0.25,{y:0.05})
				this.tl2.to(duga,3.5,{y:0.5})

				this.tl3.set(head,{rotation: -2, transformOrigin:"center"})
				this.tl3.to(head, 2, {rotation: +2, transformOrigin:"center"})
				this.tl3.to(head, 2, {rotation: -2, transformOrigin:"center"})
			},
			stop() {
				this.tl.kill();
				this.tl2.kill();
				this.tl3.kill();
			}
		}
	}

</script>

