<template>

	<div v-on:click="move" ref="CrashTea" class="anim_CrashTea">
		<slot>
			<icon-base
							width="100%"
							height="100%"
							ViewWidth="401"
							ViewHeight="401"
			><icon-tea/></icon-base>
		</slot>
	</div>

</template>

<script>
	import { TimelineMax, Power0 } from 'gsap'
	import IconTea from '../icon/iconTea.vue'
	import IconBase from '../icon/IconBase.vue'

	export default {

		data: function () {
			return {
				animobj: this.$refs.CrashTea
			}
		},
		components:{
			IconBase,
			IconTea
		},

		mounted() {
			this.animobj = this.$refs.CrashTea;
		},

		methods: {
			move() {
				this.$emit('down');
				const tl = new TimelineMax();
				tl.to(this.animobj,0.5,{rotation:270, ease: Power0.easeNone, transformOrigin:"center", y:'183'});
			},
		}
	}
</script>

