<script>
    import { TimelineMax, Power0 } from 'gsap'

    export default {
      data: function () {
        return {
          tl:undefined,
        }
      },
      mounted() {
        this.tl = new TimelineMax({repeat:-1});
      },
      props : {
        select : {
          type: Boolean,
          required: false
        },
        boom : {
          type: Boolean,
          required: false
        }
      },
      computed: {
        selectLamp() {
          return this.select;
        }
      },
      methods: {
        crash() {
          const tl = new TimelineMax();

          tl.set('#lamp_border',{opacity:0})
          tl.to('#lamp_faer', 0.1, {scale:5, transformOrigin:"center"})
          tl.to('#lamp_faer', 0.5, {scale:6, opacity:0, transformOrigin:"center"})
          tl.to('#lamp_light', 0.1, {opacity:0}, '-=0.6')
          tl.to('#lamp_verevka', 0.1  , {opacity:0}, '-=0.6')
          tl.to('#lamp_red', 1.5, {x:-300,  ease: Power0.easeNone, y:2000, rotation:100, transformOrigin:"center"}, '-=0.6')
          tl.to('#lamp_red', 0.25, {opacity:0},'-=0.4')
          tl.to('#lamp_green', 1.5, {x:300,  ease: Power0.easeNone, y:2000, rotation:-200, transformOrigin:"center"}, '-=1.5')
          tl.to('#lamp_green', 0.25, {opacity:0},'-=0.45')
        },
        start() {
          this.tl.set('#lamp_', {rotation: 4,x:-3, y:-20, transformOrigin:"start"});
          this.tl.to('#lamp_', 2.7, {rotation: -4, x:3, y:22,  ease: Power0.easeNone, transformOrigin:"start", delay:0.1});
          this.tl.to('#lamp_', 2.7, {rotation: 4, x:-3, y:-20, ease: Power0.easeNone, transformOrigin:"start", delay:0.1});
        },
        stop() {
          this.tl.kill();
        }
      }
    }

</script>

